import { Component } from "@angular/core";
import { NavController, ModalController, Platform } from "ionic-angular";
import { InAppBrowser, InAppBrowserEvent } from "@ionic-native/in-app-browser";


@Component({
  selector: "page-give",
  templateUrl: "give.html"
})
export class GivePage {
  isIOS: boolean = false;

  constructor(
    public navCtrl: NavController,
    public modalCtrl: ModalController,
    private platform: Platform,
    private iab: InAppBrowser
  ) {
    this.platform.is("ios") ? (this.isIOS = true) : false;
    console.log("----platform----- :", this.platform.is("ios"));
  }

  showModal() {
    if (this.isIOS) {
      const browser = this.iab.create(
        "https://paystack.com/pay/realmofglory",
        "_system"
      );
      browser.show();
    } else {
      let modal = this.modalCtrl.create("GiveModalPage");
      modal.present();
    }
  }

  othersG() {
    let modal = this.modalCtrl.create("OtherGivingPage");
    modal.present();
  }
}



